.subscription__wrap {
  background-image: url(../images/subscription/subscription-1.png);
  position: relative;
  margin-top: 164px;
  border-radius: 60px;
  background: #E7F9FD;
  padding: 80px 0;

  &::before,
  &::after {
    @include screen-sm {
      display: none;
    }
  }

  &::before {
    content: "";
    position: absolute;
    width: 390px;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: 0 0 0 60px;
    background: url(../images/subscription/subscription-1.png) rgba(0, 0, 0, 0) 0px 53px no-repeat;
    background-size: 100%;
  }

  &::after {
    content: "";
    position: absolute;
    width: 390px;
    height: 100%;
    top: 0;
    right: 0;
    border-radius: 0 0 30px 0;
    background: url(../images/subscription/subscriprion-2.png) transparent 0 185px no-repeat;
    background-size: 100%;
  }

  .subscription__title {

    position: relative;
    z-index: 10;

    h2 {
      font: $font-h3;
      line-height: normal;
      letter-spacing: -1.92px;
      text-align: center;

      @include screen-md {
        font: $font-h3-tablet;
      }

      @include screen-sm {
        font: $font-h3-mobile;
        text-align: center;
      }

    }

    p {
      margin: 0 auto;
      text-align: center;
      max-width: 706px;
      padding-top: 24px;
      padding-bottom: 64px;

      @include screen-sm {
        font: $font-smaller;
        text-align: center;
      }

    }


  }



}